<template>
  <div class="personal-content-body">
    <div class="content">
      <div class="content-title clear">
        <div class="left">审批列表</div>
        <el-input placeholder="搜索" v-model="keyWord" class="header-right-time right" :clearable='true' @change="getListData(1)">
          <i slot="prefix" class="el-input__icon el-icon-search "></i>
        </el-input>
      </div>
      <div class="historyapply-group">
        <div class="clear table-group-title">
          <div class="historyapply-group-top-dropdown select-normal left">
            <el-select v-model="status_str" filterable placeholder="审批类型" @change="changeApplyType">
              <el-option v-for="item in applytypelist" :key="item.id" :label="item.name" :value="item.id">
              </el-option>
            </el-select>
          </div>
          <div class="right" @click="toCreateApply">
            <div class="table-group-title-btn btn-blue">
              <img src="../../images/createapply_icon_create.png">新建审批
            </div>
          </div>
          <div class="clear"></div>
          <div class="breadcrumb">
            <el-breadcrumb separator-class="el-icon-arrow-right">
              <!-- <el-breadcrumb-item :to="{ path: '/home/administrativeManageHomepage'}" v-if="pageType==3">返回行政管理</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/home/investProjectManageHomepage' ,query: {id:projectId}}" v-if="pageType==2">返回项目首页</el-breadcrumb-item> -->
              <el-breadcrumb-item v-for="(item,index) in routeList" :key="index" :to="{name:item.name,query:item.query}">{{item.name_str}}</el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <div class="line"></div>
        </div>
        <div class="historyapply-group-tablebox">
          <div class="table-normal-box">
            <table class="table-normal">
              <tr>
                <th width="30">序号</th>
                <th width="90">审批标题</th>
                <th width="90">审批类型</th>
                <th width="100">审批申请人</th>
                <th width="120">审批申请时间</th>
                <th width="90">审批状态</th>
                <th width="90">审批事由</th>
                <th width="120">操作</th>
              </tr>
              <tr v-for="(item,index) in list" :key="item.seq">
                <td>{{pageSize*(currentPage-1)+index+1}}</td>
                <td>{{item.title}}</td>
                <td>{{item.kind}}</td>
                <td>{{item.name}}</td>
                <td>{{item.add_time}}</td>
                <td>{{item.status_str }}</td>
                <td>{{item.reasons==''||item.reasons==undefind?'无':item.reasons}}</td>
                <td>
                  <span class="table-link" @click="gotoViewapply(item.approval_id,item.status)">查看</span>
                </td>
              </tr>
            </table>
            <div class="table-nodata" v-if="total==0">
              <img src="../../images/table_nodata.png"> 没有对应数据
            </div>
          </div>
          <div class="pagination-normal" v-if="total!=0">
            <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  waitingMeApproval,
  projectLsit,
  showCopy
} from '../../config/api'
import {
  dialogConfirmHasbtn,
  dialogConfirm
} from '../../config/mUtils'
export default {
  data() {
    return {
      list: [],
      routeList: [],
      pageType: 1, //1=>一级2=>项目管理页面进入3=>行政管理页面进入
      projectId: '', //公司id
      projInfo: '',
      pageSize: 10,
      keyWord: '',
      total: 0,
      aType: 1, //审批类型1=>出勤休假, 2=>用印审批 ,3=>签字审批
      template_id: '', //模版id
      status: 1, //审批类型
      status_str: '待我审批', //审批类型
      applytype1: 1, //申请类型（废弃）
      applytypeval: '待我审批',
      applytypelist: [{
        id: 1,
        name: '待我审批'
      }, {
        id: 2,
        name: '我已审批'
      }, {
        id: 3,
        name: '我发起的审批'
      }, {
        id: 4,
        name: '抄送我的'
      }],
      applytypelist1: [{
        id: 1,
        name: '出勤审批'
      }, {
        id: 2,
        name: '用印审批'
      }],
      currentPage: 1,
    }
  },
  watch: {
    $route(to, from) {
      this.initData();
    }
  },
  created() {
    if (this.routeList.length != 0) {} else {
      this.routeList = this.$route.meta.routeList;
      // console.log(this.routeList);
      this.routeList.forEach(element => {
        if(element.name_str=='行政管理'){
          element.name_str = this.$route.query.aName;
        }
      });
    }
    if (this.$route.query.page) {
    		this.currentPage = parseInt(this.$route.query.page);
    }
    this.initData();
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      //vm.routeList = to.meta.routerList
    });
  },
  beforeRouteLeave(to, from, next) {
    next((vm) => {

    });
  },
  mounted() {},
  methods: {
    initData() {
      if (!this.$route.query.from) {
        this.$router.push({
          name: 'administrativeManageHomepage'
        })
      } else {
        this.pageType = this.$route.query.from
      }
      if (this.pageType == 2) {
        if (!this.$route.query.projectId) {
          this.$router.push({
            name: 'investProjectManageList'
          })
        } else {
          this.projectId = this.$route.query.projectId;
          //获取项目信息
          projectLsit({
            page: 1,
            number: 10,
            id: this.projectId
          }).then(res => {
            if (res.data.msgNo == 1) {
              this.projInfo = res.data.payload.data[0]
            }
          })
        }
      } else if (this.pageType == 3) {
        if (!this.$route.query.template_id) {
          this.template_id = ''
        } else {
          this.template_id = this.$route.query.template_id;
        }
      }
      //获取筛选类型（待我审批/我已审批/我发起的审批）
      // if (!this.$route.query.status) {
      //   this.status = 1;
      // } else {
      //   this.status = this.$route.query.status;
      // }
      // switch (this.status) {
      //   case 1:
      //     this.status_str = "待我审批";
      //     break;
      //   case 2:
      //     this.status_str = "我已审批";
      //     break;
      //   case 3:
      //     this.status_str = "我发起的审批";
      //     break;
      // }
      //获取模版类别（1=>出差2=>用印3=>签字）
      if (!this.$route.query.aType) {
        this.aType = '';
      } else {
        this.aType = this.$route.query.aType
      }
      this.routeList.forEach(element => {
        if(element.name_str=='审批列表'){
          if(!element.query.status){
            this.status = 1;
            this.status_str = "待我审批";
            this.getListData(1);
          }else{
            this.status = element.query.status;
            switch (this.status) {
              case 1:
                this.status_str = "待我审批";
                break;
              case 2:
                this.status_str = "我已审批";
                break;
              case 3:
                this.status_str = "我发起的审批";
                break;
              case 4:
                this.status_str = "抄送我的";
                break;
            }
            this.getListData(element.query.current);
          }
        }
      });
      
    },
    currentChange(val) {
      if (this.total > this.pageSize) {
        this.getListData(val)
      }
    },
    //获取数据
    getListData(val) {
      this.list = [];
      this.currentPage = val;
      if (this.status == 4) {
        let params = {
          page: this.currentPage,
          pageSize: this.pageSize,
          fuzzyQuery: this.keyWord,
          template_id: this.template_id
        }
        showCopy(params).then(res => {
          this.changeRouterData();
          let data = res.data.payload;
          this.total = data.total;
          if (data.data.length != 0) {
            data.data.forEach(item => {
              if (item.status == 0) {
                item.status_str = '待审批'
              } else if (item.status == 1) {
                item.status_str = '审批中'
              } else if (item.status == 2) {
                item.status_str = '审批通过'
              } else if (item.status == 3) {
                item.status_str = '待用印'
              } else if (item.status == 4) {
                item.status_str = '完成用印'
              } else if (item.status == 5) {
                item.status_str = '撤回审批'
              } else if (item.status == -1) {
                item.status_str = '审批不通过'
              }
            })
            this.list = data.data;
          }
          
        })
      } else {
        let params = {
          page: this.currentPage,
          pageSize: this.pageSize,
          status: this.status,
          fuzzyQuery: this.keyWord,
          type: this.aType,
          template_id: this.template_id,
          company_id: this.projectId
        }
        waitingMeApproval(params).then(res => {
          this.changeRouterData();
          let data = res.data.payload;
          this.total = data.total;
          if (data.data.length != 0) {
            this.list = data.data;
          }
        })
      }


    },
    changeRouterData(){
      // console.log(this.routeList);
      this.routeList.forEach(element => {
        if(element.name_str=='审批列表'){
          let newObj = element.query;
          newObj.status = this.status;
          newObj.current = this.currentPage;
          this.$router.push({
            name: element.name,
            query: newObj,
          })
        }
        
      });
      // console.log(this.routeList);
    },
    changeApplyType(val) {
      // console.log(val);
      // console.log(this.status_str);
      this.status = val;
      this.getListData(1);
    },
    changeApplyType1(val) {
      this.getListData(1);
    },
    //前往查看页面
    gotoViewapply(id, status) {
      if (this.status == 1 || this.status == 2) {
        status = 2
      } else {
        status = 1
      }
      if (this.pageType == 2) {
        this.$router.push({
          name: 'viewApply',
          query: {
            approval_id: id,
            status: status,
            from: this.pageType,
            projectId: this.projectId,
            page: this.currentPage
          }
        })
      } else {
        this.$router.push({
          name: 'viewApply',
          query: {
            approval_id: id,
            status: status,
            from: this.pageType,
            template_id: this.$route.query.template_id,
            aType: this.$route.query.aType,
            aName: this.$route.query.aName,
            page: this.currentPage
          }
        })
      }
    },
    //新建审批
    toCreateApply() {
      if (this.pageType == 2) {
        this.$router.push({
          name: 'createApplyHomepage',
          query: {
            projectId: this.projectId
          }
        })
      } else {
        this.$router.push({
          name: 'createApply',
          query: {
            template_id: this.$route.query.template_id,
            aType: this.$route.query.aType,
            aName: this.$route.query.aName,
            from: 3
          }
        })
      }
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal.scss';
.content {
  width: 100%;
  min-height: 100%;
  min-width: 1000px;
  background-color: white;
  padding: 36px 40px;
}

.content-title {
  width: 100%;
}

.histroyapply-icon {
  width: 15px;
  height: 15px;
  background: url('../../images/histroyapply_icon.png') no-repeat;
  display: inline-block;
  float: left;
  margin: 2px 6px 0px 0px;
}

.historyapply-group {
  width: 100%;
  border: 1px solid #E4E4E4;
  padding: 18px 0 16px;
}

.historyapply-group-total {
  font-size: 18px;
  color: #3C98E8;
  font-weight: bold;
  line-height: 1em;
  margin-bottom: 35px;
}

.historyapply-group-tablebox {
  // padding: 0px 18px 0px 14px;
  padding: 0px 50px;
  min-height: 440px;
}

.table-normal-box {
  min-height: 440px;
}

.pagination-normal {
  text-align: center;
  margin-top: 20px;
}

.header-right-time {
  margin-right: 10px;
  position: relative;
}

.header-right-time:nth-child(2) {
  margin-right: 0px;
}

.creditCz-box span:hover {
  cursor: pointer;
  color: #3C98E8;
}

.creditCz-box span:nth-child(2) {
  //padding: 0 10px;
}

.btn-cancle {
  border: 1px solid #3C98E8;
  color: #3C98E8;
  margin-right: 20px;
}

.form-group-inputbox {
  width: 470px;
  margin-right: 0px;
}

.breadcrumb {
  margin-top: 19px;
  padding: 0px 14px;
}

.table-group-title-btn {
  display: flex;
  align-items: center;
  img {
    margin-right: 8px;
  }
}

.historyapply-group-top-dropdown {
  margin-right: 10px;
}

</style>



